import React, { useEffect, useState } from "react";
// @mui
import { Container, Grid, Box } from "@mui/material";
import { useTheme } from "@mui/material/styles";
// hooks
import useAuth from "../../hooks/useAuth";
import useSettings from "../../hooks/useSettings";
// components
import Page from "../../components/Page";
// sections
import {
  AppMiniReport,
  AppMiniReport2,
  AppMiniReport3,
  AppMiniReport4,
  TransactionStatistics,
  TransactionStatistics2,
  TransactionAgent,
  TransactionAgent2,
} from "../../sections/@dashboard/general/app";
import { MotionContainer } from "../../components/animate";
import {
  BookingIllustration,
  CheckInIllustration,
  CheckOutIllustration,
} from "../../assets";
import { getAppReport, getAppTicketReport } from "../../redux/slices/generale";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "../../redux/store";

// ----------------------------------------------------------------------

export default function GeneralApp() {
  const { user } = useAuth();
  const { t } = useTranslation();
  const { themeStretch } = useSettings();

  const [load_app_ticketreport, setLoad_app_ticketreport] = useState(false);
  const dispatch = useDispatch();

  const { appreport, appticketreport } = useSelector((state) => state.generale);

  useEffect(() => {
    dispatch(getAppReport());
  }, [dispatch]);

  const handleRefresh = async () => {
    setLoad_app_ticketreport(true);
    await dispatch(getAppTicketReport());
    setLoad_app_ticketreport(false);
  };

  return (
    <Page title="Home: Dashboard">
      <RemesasBg>
        <Container
          component={MotionContainer}
          maxWidth={themeStretch ? false : "xl"}
        >
          <Grid container spacing={2}>
            <Grid item xs={12} md={3}>
              <AppMiniReport4
                t={t}
                bgColor="#4A90E2"
                title={t("app.total_balance")}
                total={appreport?.balance?.balance || 0}
                total1={appreport?.balance_htg || 0}
                total2={appreport?.balance_clp || 0}
                currency="USD"
                currency1="HTG"
                currency2="CLP"
                percent={100}
                icon={<BookingIllustration />}
              />
            </Grid>

            <Grid item xs={12} md={3}>
              <AppMiniReport
                t={t}
                bgColor="#3FBAC2"
                title={t("app.total_ticket")}
                total={appreport?.tickets}
                percent={appreport?.percentage_progress?.tickets}
                icon={<CheckInIllustration />}
              />
            </Grid>

            <Grid item xs={12} md={3}>
              <AppMiniReport
                t={t}
                bgColor="#E18237"
                title={t("app.total_montant")}
                total={appreport?.montants}
                percent={appreport?.percentage_progress?.montants}
                icon={<CheckOutIllustration />}
              />
            </Grid>

            <Grid item xs={12} md={3}>
              <AppMiniReport
                t={t}
                bgColor="#7B61FF"
                title={t("app.total_commission")}
                total={appreport?.commissions}
                percent={appreport?.percentage_progress?.commissions}
                icon={<BookingIllustration />}
              />
            </Grid>

            <Grid item xs={12}>
              <TransactionStatistics2
                t={t}
                appticketreport={appticketreport}
                handleRefresh={handleRefresh}
                load_app_ticketreport={load_app_ticketreport}
              />
            </Grid>
          </Grid>
        </Container>
      </RemesasBg>
    </Page>
  );
}

// ----------------------------------------------------------------------

const RemesasBg = ({ children }) => {
  const theme = useTheme();

  return (
    <Box
      sx={{
        position: "relative",
        overflow: "hidden",
        borderRadius: 2,
        px: { xs: 2, sm: 3, md: 4 },
        py: { xs: 2, sm: 3, md: 4 },
        background:
          "linear-gradient(135deg, #bed7f3ff 0%, #F7FBFF 45%, #9cbae7ff 100%)",
        boxShadow: "0 8px 45px rgba(15, 35, 95, 0.16)",
        "&::before": {
          content: '""',
          position: "absolute",
          inset: -80,
          background:
            "radial-gradient(circle at 0% 0%, rgba(120,180,255,0.32), transparent 55%), \
             radial-gradient(circle at 100% 100%, rgba(123,97,255,0.26), transparent 55%)",
          opacity: 0.95,
          zIndex: 0,
        },
      }}
    >
      <Box
        sx={{
          position: "relative",
          zIndex: 1,
        }}
      >
        {children}
      </Box>
    </Box>
  );
};
